import { IManagedObject } from '@c8y/client';

export interface SigfoxDevice {
  id: string;
  pac: string;
  deviceType: IManagedObject;
  contractId: string;
  prototype: boolean;
  productCertificate: string;
  error?: string;
  lnsConnectionName: string;
}

export interface Contract {
  id: string;
  name: string;
}

export interface SigfoxDeviceFormly extends SigfoxDevice {
  contract: Contract;
  connection: {
    name: string;
    description: string;
    baseUrl: string;
    parentGroupId: string;
    username: string;
    password: string;
  };
}

export const PRODUCT_EXPERIENCE = {
  EVENT: 'deviceRegistration',
  COMPONENT: 'sigfox-registration',
  RESULT: { SUCCESS: 'registrationSuccess', FAILURE: 'registrationFailure' }
};
